import React from 'react';
import { connect } from 'react-redux';
import grade from '../../../Static/Img/grade.svg';
import s from './SectionFour.module.css';

const SectionFour = (props) => {
    return <div className={s.sectionFour}>
        <h2 className={s.title}>Essentials to complement your routine</h2>
        <div className={s.grid}>
            { props.products.map((el) => { return <div className={s.grid__box}>
                <div className={s.box__wrapImg}>
                    <img className={s.box__img} src={el.img} alt="product"/>
                </div>
                <div className={s.benefits}>
                    <h2 className={s.benefits__title}>{el.title}</h2>
                    <h3 className={`${s.benefits__subtitle} ${s.benefits__subtitle_Weight300}`}>{el.description}</h3>
                    <p className={s.benefits__price}>{el.price}</p>
                        <div className={s.benefits__grade}>
                            <img className={s.grade__points} src={grade} alt="points"/>
                            <p className={s.grade__prestige}>100 Reviews</p>
                        </div>
                    <button className={s.button}>Add to cart</button>
                </div>
            </div>
            })}
        </div>
    </div>
}   

const mapStateToProps = (state) => {
    return {
        products: state.productsPage.productsRecommended,
    }
}    
export default connect(mapStateToProps)(SectionFour);