import React from "react";
import { connect } from 'react-redux';
import s from "./Reviews.module.css";

const Message = (props) => {
  return (
    <div className={s.message}>
      {props.name}
      {props.review}
    </div>
  );
};

const Name = (props) => {
  return <p className={s.name}>{props.name}</p>;
};

const Review = (props) => {
  return <p className={s.review}>{props.review}</p>;
};

const Reviews = (props) => {
  console.log(props)
  return <div className={s.reviews}>
      <h2 className={s.title}>Reviews about yours</h2>
      { props.reviews.map((el) => { return <div className={s.wrap}>
        
        <Message
          name={<Name name={el.name} />}
          review={<Review review={el.review} />}
        />
      </div>})}
    </div>
};

const mapStateToProps = (state) => {
  return {
    reviews: state.reviewsPage.reviewsData,
  }
} 
export default connect(mapStateToProps)(Reviews);
